import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { UserService } from "../_services/user.service";
import { FileService } from "../_services/file.service";



@Component({
    moduleId: module.id,
    selector: 'saved-files',
    templateUrl: 'saved-files.component.html',
    styleUrls: ['saved-files.component.css']
})
export class SavedFilesComponent implements OnInit {
    savedFilesArray: any = [];
    constructor(
        private userService: UserService,
        private fileService: FileService,
        private router: Router,
    ) {}

    ngOnInit() {
        var id: string = JSON.parse(localStorage.getItem('user'));
        console.log(this.userService.getUserFiles(id));
        this.userService.getAllArchiveFiles(id).subscribe(data => {
            this.savedFilesArray = data;
            this.savedFilesArray.reverse();
        });
    }

    downloadFile(filename: string){
        console.log("filename -> ", filename);
        this.userService.downloadArchive(filename).subscribe(data => {
            console.log(data);
            this.parseFile(data);
        })

    }
    parseFile(data: any){
        var blob = new Blob([data], {type: 'text/csv'});
        var url = window.URL.createObjectURL(blob);
        window.open(url);
    }
}
